*, *::after, *::before{
    box-sizing: border-box;
}

body{
    background-color: #333;
    margin: 0;
    overflow: hidden;
}

.paddle{
    --pos: 50;
    
    position: absolute;
    background-color: whitesmoke;
    height: 20vh;
    width: 1.5vh;
    top: calc(var(--pos)*1vh);
    transform: translateY(-50%);
}

.left{
    left: 0vw;
}

.right{
    right: 0vw;
}

.score{ 
    display: flex;
    justify-content: center;
    font-weight: bold;
    font-size: 7vh;
    color: turquoise;
}

.score > *{
    flex-grow: 1;
    flex-basis: 0;
    padding: 0 2vh;
    margin: 1vh 0;
}
.score > :first-child{
    text-align: right;
    border-right: .5vh solid white;
}

.ball{
    --x: 50;
    --y: 50;
    
    position: absolute;
    background-color: whitesmoke;
    height: 5vh;
    width: 5vh;
    top: calc(var(--y)*1vh);
    left: calc(var(--x)*1vw);
    transform: translate(-50%, -50%);
    border-radius: 50%;
}